.main {
  color: var(--text-primary);
  background-color: var(--background);
  font-size: large;
  font-weight: bold;
  padding: 20px;
  height: calc(100vh - 40px);
  transition: all 0.5s;
}

.switcher {
  color: var(--text-primary);
  background-color: var(--background);
  float: right;
  transition: all 0.5s;
  outline: none;
  border: none;
}

.icon {
  color: var(--text-primary);
}

.home-button {
  float: right;
}

.header {
  padding-bottom: 48px;
}

.mid-section {
  max-width: 600px;
  width: 80%;
  margin: 0 auto;
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
}

.links-section {
  justify-content: space-between;
}

.section {
  display: flex;
}

.section-title {
  flex-grow: 0;
  padding-right: 32px;
  color: var(--text-secondary);
}

.section-text {
  flex-grow: 1;
}

.link {
  color: inherit;
  padding-right: 12px;
}

.styled-link {
  color: inherit;
  text-decoration: none;
  background-color: var(--accent);
}

.akunacapital:hover {
  transition: 400ms;
  background-color: rgb(11, 46, 114);
  color: white;
}

.citadel:hover {
  transition: 400ms;
  background-color: rgb(8, 35, 90);
  color: white;
}

.meta:hover {
  transition: 400ms;
  background-color: rgb(0, 99, 191);
  color: white;
}

.nuro:hover {
  transition: 400ms;
  background-color: rgb(49, 49, 49);
  color: white;
}

.tesla:hover {
  transition: 400ms;
  background-color: rgb(232, 33, 39);
  color: white;
}

.toronto:hover {
  transition: 400ms;
  background-color: rgb(0, 42, 92);
  color: white;
}

.iai:hover {
  transition: 400ms;
  background-color: rgb(48, 140, 251);
  color: white;
}

.cat:hover {
  transition: 400ms;
  background-color: rgb(180, 156, 128);
  color: white;
}
